"use client"

const CTAButton = ({active,text,className}:{active:boolean,text:string,className?:string}) => {
  return (
    <button className={`btn h-12 rounded-lg pt-3 pb-3 px-6 py-6 font-medium text-[16px] leading-6
    btn-xs sm:btn-sm md:btn-md lg:btn-lg xl:btn-xl ${active ? 'bg-[var(--yellow-50)]':'bg-[var(--richblack-800)]'} ${className}`}>
      {text}
    </button>
  )
}

export default CTAButton;